.list{
    color: #fff;
    width: 300px;
    background-color: #bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px
}

.list p{
    padding: 10px;
    display: flex
}
.buttonSpan{
    display: contents
}

.buttonSpan button{
    color: #fff;
    background-color:blue;
    width:80px;
    height:30px;
    padding:4px;
    border-radius:10px;
    border:0px
    

}
.buttonSpan button:focus{
    outline: none;
    color: #fff;
    background-color:blue;
    width:80px;
    height:30px;
    padding:4px;
    border-radius:10px;
    border:0px
}
.list p input{
    background-color: transparent;
    border:0;
    color:#fff;
    font-size: 18px;
    width: fit-content

}
.list p input:focus{
    outline: none
}